import React, { useState } from "react";
import "./Mockup.css";
import { Form, Stack, Spinner, Alert, Table } from "react-bootstrap";
import CustomButton from "../../components/Button/Button";
import FileUpload from "../../components/FileUploader/FileUploader";
import CustomModal from "../../components/Modal/customModal";
import { downloadTemplate } from "../../utils/downloadTemplate";
import useToast from "../../hooks/useToast";
import * as XLSX from "xlsx";
import config from "./config.json";
import Loader from "../../components/Loader/Loader";
import ErrorPopup from "../../components/ErrorPopup/ErrorPopup";

const MassUpload = () => {
  const [modalShow, setModalShow] = useState(false);
  const [errorModalShow, setErrorModalShow] = useState(false);
  const [showContentErrors, setShowContentErrors] = useState(false);
  const { showErrorToast, showSuccessToast } = useToast();
  const [isValidating, setIsValidating] = useState(false);
  const [operationChoice, setOperationChoice] = useState("");
  const [errors, setErrors] = useState([]);

  // Function to validate the uploaded file
  const validateFile = (file) => {
    return new Promise((resolve, reject) => {
      const reader = new FileReader();
      reader.readAsBinaryString(file);

      reader.onload = (e) => {
        const workbook = XLSX.read(e.target.result, { type: "binary" });
        const sheetName = workbook.SheetNames[0];
        const worksheet = workbook.Sheets[sheetName];

        // 🟢 Extract headers explicitly even if there is no row data
        const headers = XLSX.utils.sheet_to_json(worksheet, { header: 1 })[0];

        if (!headers || headers.length === 0) {
          reject(["The uploaded file has no content or Empty."]);
          return;
        }
        // 🔥 Extract actual sheet data (excluding headers)
        const sheetData = XLSX.utils.sheet_to_json(worksheet, { defval: "" });
        console.log("sheetData", sheetData);

        if (sheetData.length === 0) {
          reject([
            "The uploaded file contains only headers but no data. Please add at least one row.",
          ]);
          return;
        }

        // Extract headers from first row
        const fileHeaders = Object.keys(sheetData[0]);

        // 🔥 **NEW: Check for duplicate headers**
        const headerCounts = {};
        fileHeaders.forEach((header) => {
          headerCounts[header] = (headerCounts[header] || 0) + 1;
        });

        const duplicateHeaders = Object.keys(headerCounts).filter(
          (header) => headerCounts[header] > 1
        );

        if (duplicateHeaders.length > 0) {
          reject([`Duplicate headers found: ${duplicateHeaders.join(", ")}`]);
          return;
        }

        // Determine matching operation based on headers
        let matchedOperation = null;
        for (const [key, value] of Object.entries(config.MassUpload)) {
          if (
            value.fileHeaders.every((header) => fileHeaders.includes(header))
          ) {
            matchedOperation = key;
            break;
          }
        }

        if (!matchedOperation) {
          reject(["File headers do not match any expected template."]);
          return;
        }

        const expectedHeaders = config.MassUpload[matchedOperation].fileHeaders;
        console.log("Expected Headers", expectedHeaders);
        const mandatoryAttributes =
          config.MassUpload[matchedOperation].mandatoryAttributes;

        console.log("Mandatory Attributes", mandatoryAttributes);
        // Validate missing headers
        const missingHeaders = expectedHeaders.filter(
          (header) => !fileHeaders.includes(header)
        );
        if (missingHeaders.length > 0) {
          reject([`Missing required headers: ${missingHeaders.join(", ")}`]);
          return;
        }

        // Validate mandatory fields are not empty
        const validationErrors = [];
        sheetData.forEach((row, index) => {
          mandatoryAttributes.forEach((field) => {
            if (!row[field] || row[field].toString().trim() === "") {
              validationErrors.push(
                `Row ${index + 2}: "${field}" is required but is empty.`
              );
            }
          });
        });

        if (validationErrors.length > 0) {
          console.log("Rejecting with validation errors:", validationErrors); // ADDED LOG HERE
          reject(validationErrors);
        } else {
          console.log("Resolving validation"); // ADDED LOG HERE
          resolve();
        }
      };

      reader.onerror = () => reject(["File reading failed."]);
    });
  };

  const handleFileUpload = (files) => {
    if (files.length === 0) return;

    setIsValidating(true);
    validateFile(files[0])
      .then(() => {
        console.log("Validation successful (inside .then)");
        setErrors([]);
        setIsValidating(false);
        showSuccessToast(" No Error File validated successfully!"); // Show success toast
      })
      .catch((validationErrors) => {
        console.log("Validation failed (inside .catch", validationErrors);
        setErrors(validationErrors);
        setIsValidating(false);
        setErrorModalShow(true);
      });
  };

  const handleDownloadTemplate = () => {
    downloadTemplate(showErrorToast)(operationChoice);
  };

  const handleReset = () => {
    // Reset all states to their initial values
    setModalShow(false);
    setErrorModalShow(false);
    setIsValidating(false);
    setOperationChoice("");
    setErrors([]);

    // Reset the dropdown
    const selectElement = document.querySelector(
      'select[aria-label="Choose Operations"]'
    );
    if (selectElement) {
      selectElement.value = "";
    }

    // Show toast message
    showSuccessToast("Widget reset successfully!");
  };

  console.log("Operation Choice:", errors);

  return (
    <>
      <Stack className="mt-3" gap={4}>
        {/* Choose operation and download template */}
        <Stack direction="horizontal">
          <Form.Select
            aria-label="Choose Operations"
            style={{
              fontSize: "11px",
              width: "30%",
              border: "1px solid #007bff",
              outline: "none",
              borderRadius: "8px",
              fontWeight: "bold",
            }}
            onChange={(e) => setOperationChoice(e.target.value)}
          >
            <option value="">Choose Operations</option>
            <option value="1">Physical Product/Raw Material</option>
            <option value="2">Physical Product Structure</option>
            <option value="3">Document</option>
            <option value="4">Physical Product-Document</option>
          </Form.Select>

          <CustomButton
            variant="link ms-auto "
            onClick={handleDownloadTemplate}
            text="Download Template"
          />
        </Stack>

        {/* Drag and Drop File Upload */}
        <FileUpload
          fileTypes={["XLSX"]}
          multiple={false}
          onUpload={handleFileUpload}
          onReset={handleReset}
        />

        {/* Show loader while validating */}
        {isValidating && <Loader />}

        {/* Submit Button and Content Error Button */}
        <Stack direction="horizontal" gap={2}>
          <Form.Check type="checkbox" label="Background" />
          <div className="ms-auto d-flex gap-5">
            {errors.length > 0 && (
              <CustomButton
                variant="danger"
                onClick={() => setShowContentErrors(true)}
                text={`Content Errors (${errors.length})`}
              />
            )}
            <CustomButton
              variant="primary"
              onClick={() => setModalShow(true)}
              text="Submit"
            />
          </div>
        </Stack>
      </Stack>

      {/* Content Errors Modal */}
      <CustomModal
        show={showContentErrors}
        onHide={() => setShowContentErrors(false)}
        title="Content Errors"
        footerButtons={[
          {
            label: "Close",
            variant: "danger",
            onClick: () => setShowContentErrors(false),
          },
        ]}
      >
        <ErrorPopup errors={errors} />
      </CustomModal>

      {/* Confirmation Modal */}
      <CustomModal
        show={modalShow}
        onHide={() => setModalShow(false)}
        title="Custom Modal Title"
        footerButtons={[
          {
            label: "Cancel",
            variant: "danger",
            onClick: () => setModalShow(false),
          },
          {
            label: "Confirm",
            variant: "success",
            onClick: () => alert("Confirmed!"),
          },
        ]}
      >
        <p>This is a reusable modal component with dynamic content.</p>
      </CustomModal>
    </>
  );
};

export default MassUpload;
